#project {
    height: 96%;
}

#project-branch-switch {
    margin-top: 2%;
    display: flex;
    justify-content: center;
}

#project-major-minor-switch {
    margin-top: 2%;
    display: flex;
    justify-content: center;
}

.project-tab-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    border-radius: 27px;
    border: 1px solid rgb(146, 146, 146);
    line-height: 0;
}

.project-tab-list-item {
    display: inline-block;
    font-size: 13px;
    cursor: pointer;
    font-family: "Roboto", sans-serif;
    padding: 20px 20px;
    overflow: hidden;
    transition: all .3s ease;
}

.project-tab-list-active {
    background-color: #2e3133;
    -webkit-border-radius: 27px;
    border-radius: 27px;
    color: #f1f1f1; 
}

#projects {
    background-color: #d3d3d3;
    color: white;
    height: 100%;
    width: 98%;
    margin-top: 2%;
    margin-left: 1%;
    overflow: scroll;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    border-radius: 10px;
}

.projects-tiel {
    flex: 0 0 360px;
}

.projects-tiel figure {
    position: relative;
    overflow: hidden;
    border-radius: 10px;
    margin: 5%;
}

.projects-tiel figure img {
    max-width: 100%; 
    max-height: 100%;
    object-fit: contain;
}

.projects-tiel figure figcaption .projects-tiel-link {
    display: flex;
    position: absolute;
    font-size: 18px;
    right: 5%;
    gap: 10px;
}

.projects-tiel figure figcaption h4 {
    margin-top: 0px;
    font-size: 18px;
    font-weight: 600;
    padding-left: 2%;
}

.projects-tiel figure figcaption p {
    font-size: 13px;
    margin-top: 20px;
    padding-left: 2%;
}

.projects-tiel figure figcaption .projects-tiel-tech {
    display: flex;
    justify-content: left;
    position: absolute;
    bottom: 25%;
}

.projects-tiel figure:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: #000;
    opacity: 0;
    transition: all .4s ease-in-out; 
}

.projects-tiel figure:hover:before {
    opacity: .7; 
}

.projects-tiel figure:hover figcaption h4, .projects-tiel figure:hover figcaption p, .projects-tiel figure:hover figcaption .projects-tiel-link, .projects-tiel figure:hover figcaption .projects-tiel-tech {
    opacity: 1;
    transition: all .4s ease-in-out;
}

.projects-tiel figure figcaption {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 15%;
    color: #fff;
}

.projects-tiel figure figcaption h4, .projects-tiel figure figcaption p, .projects-tiel figure figcaption .projects-tiel-link, .projects-tiel figure figcaption .projects-tiel-tech {
    opacity: 0;
    color: #fff;
    transition: all .1s ease-in; 
}
